<template>
    <div>
        <h2>ຜົນງານ ( {{ portfolios.length }} )</h2>
        <v-row wrap>
            <v-col xs="12" sm="6" md="4" lg="3"
                   v-for="(port, index) in portfolios" :key="index">
                <v-card outlined class="portfolio-card mx-auto">
                    <router-link :to="{ name: 'DashboardPortfolioUpdate' , params: { id: port._id } }">
                        <v-img
                                class="portfolio-img"
                                :src="port.images ? $server + port.images[0] : ''"
                                height="200px">
                        </v-img>
                    </router-link>
                    <v-card-title>
                        <span class="ml-3 subtitle-1">{{ port.title }}</span>
                    </v-card-title>
                    <!--                    <v-card-text v-html="port.description.substring(0,20) + '...'"></v-card-text>-->
                    <hr>
                    <v-card-actions>
<!--                        <v-avatar>-->
<!--                            <v-img  width="40"-->
<!--                                    height="40"-->
<!--                                    :src="port.author.image ? $server + port.author.image:'https://www.w3schools.com/howto/img_avatar.png'"-->
<!--                            ></v-img>-->
<!--                        </v-avatar>-->
                        <p @click="removePortfolio(port._id, index)" class="text-right"><i
                                class="fas fa-trash-alt fa-lg btn-delete mr-1"></i></p>
                        <v-btn @click="filterCate(port.categoryId._id)"
                               class="primary--text font-weight-bold ml-auto v-btn--outlined"
                               text>
                            {{
                            port.categoryId ?
                            port.categoryId.name: ''
                            }}</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
            <v-col v-if="portfolios.length <= 0 && auth.type === 'freelancer'">
                <p>ຍັງບໍ່ທັນມີຜົນງານ
                    <router-link
                        :to="{ name: 'DashboardPortfolioPost' }"
                    >
                    ເພີ່ມຜົນງານ</router-link> </p>
            </v-col>
            <v-col v-else-if="auth.type === 'employer'">
                <router-link :to="{ name: 'DashboardProfileEdit' }">ສະໝັກເປັນ Freelance</router-link>
            </v-col>
        </v-row>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        data: () => ({
            portfolios: []
        }),
        computed: {
            ...mapGetters([
                'auth'
            ])
        },
        methods: {
            async fetchPortfolio() {
                const userId = this.auth._id
                const results = await this.$axios.get('portfolio/getall', { params: { userId } })
                const portfolios = results.data.portfolios
                this.portfolios = portfolios
            },
            async detail (id) {
                 this.$router.push({ name: 'PortfolioEdit', params: { id: id } })
            },
             async removePortfolio (id, index) {
                 const willDelete = await this.$swal({
                     title: 'ແນ່ໃຈທີ່ຈະລົບ Portfolio ແທ້ບໍ່?',
                     text: 'ລົບແລ້ວຈະບໍ່ສາມາດກູ້ຄືນໄດ້',
                     type: 'warning',
                     showCancelButton: true,
                     confirmButtonColor: '#d33',
                     cancelButtonColor: '#000',
                     confirmButtonText: 'ເຂົ້າໃຈແລ້ວ, ລົບ!!',
                     cancelButtonText: 'ບໍ່ລົບ, ກັບຄືນ'
                 })
                 if(willDelete.value) {
                     const portId = id
                     await this.$axios.delete('portfolio/delete' , { params: { _id: portId } } )
                     this.portfolios.splice(index, 1)
                     this.$swal({
                         title: 'ລົບ Portfolio ແລ້ວ',
                         type: 'success',
                         toast: true,
                         position: 'top',
                         showConfirmButton: false,
                         timer: 2000,
                     })
                     this.$io.emit('deletePortfolio', portId)
                 }
            }
        },
        created () {
            this.fetchPortfolio()
        }
    }
</script>

