    .portfolio-card {
        border: 0.5px solid whitesmoke;
        &:hover {
            transform: scale(1.015);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        }
    }
    .img-portfolio {
        border: 0.5px solid black;
        border-radius: 5px;
        width: 250px;
        height: 200px;
    }
    .title-portfolio {
        font-size: 20px;
        font-weight: bold;
    }
    .btn-category {
        text-align: end;
        margin-right: 10px;
    }
    .btn-edit {
        &:hover {
            transform: scale(1.3);
            color: deepskyblue;
        }
    }
    .btn-delete {
        transform: scale(1.1);
        &:hover {
            transform: scale(1.3);
            color: red;
        }
    }
</style>
